body{
  background-color: gray;
}
header{
  margin: 0px 14px;
  display: flex;
  color: pink;
}
.nav_links li{
  list-style: none;
  text-align: center;
  color: aliceblue;
  margin:0 9px;
  cursor: pointer;
}
.nav_links li:hover{
  color: skyblue;
  transition: 500ms;
}
.nav_links{
  margin: 20px 39rem;
  display: inline-flex;
}
.container {
  background-color: white;
  padding: 20px;
  margin: 0px 14rem;
  text-align: left;
  border-radius: 10px;
}
.container h3{
  text-align: center;
  background-color: white;
}
#Work-Name{
  border-radius: 6px;
  width: 597px;
  height: 20px;  
  background-color: antiquewhite;
}
#description{
  border-radius: 6px;
  resize: none;
  width: 600px;
  height: 100px;
  background-color: antiquewhite;
}
.todo{
  margin-top: 20px;
  background-color: white
}
#todo-table{
  border-collapse: collapse;
  background-color: white;
  width: 50rem;
  border-radius: 100px;
  text-align: center;
}
.Footer{
  margin-top: 40%;
}
